/* You can add global styles to this file, and also import other style files */
@font-face {
  font-family: VerminVibes;
  src: url(assets/fonts/Vermin-Vibes-V.ttf.eot);
  src: url(assets/fonts/Vermin-Vibes-V.ttf.svg);
  src: url(assets/fonts/Vermin-Vibes-V.ttf.woff);
}
:root {
  --fontFamily: "Teko", sans-serif;
  --fontFamily2: VerminVibes;
  --fontFamily3: "Poppins", sans-serif;
  --mainColor: #fc0820;
  --whiteColor: #ffffff;
  --blackColor: #0c0305;
  --fontSize: 18px;
  --transition: .5s;
}

body {
  background-color: var(--blackColor);
  color: var(--whiteColor);
  padding: 0;
  margin: 0;
  overflow-x: hidden;
  font-family: var(--fontFamily);
  font-size: var(--fontSize);
}

a {
  color: var(--whiteColor);
  transition: var(--transition);
  outline: 0 !important;
  text-decoration: none;
}
a:hover {
  color: var(--mainColor);
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1210px;
}

:focus {
  outline: 0 !important;
}

.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  color: var(--whiteColor);
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.pt-100 {
