}

p {
  margin-bottom: 15px;
  line-height: 1.8;
  font-family: var(--fontFamily3);
  font-size: 15px;
}
p:last-child {
  margin-bottom: 0;
}

.bg-1b060a {
  background-color: #1b060a;
}

.d-table {
  width: 100%;
  height: 100%;
}
.d-table-cell {
  vertical-align: middle;
}

/*animate-cursos*/
.zinble-cursor {
  width: 7px;
  height: 7px;
  background-color: var(--mainColor);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  transition: none;
  Pointer-events: none;
  z-index: 9999999999;
}

.zinble-cursor2 {
  position: absolute;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  border: 1px solid var(--mainColor);
  position: absolute;
  top: 9px;
  left: 9px;
  transition: none;
  Pointer-events: none;
  z-index: 9999999999;
}

/*default&optional-btn*/
.default-btn {
  text-transform: uppercase;
  border: none;
  transition: var(--transition);
  display: inline-block;
  background-color: var(--mainColor);
  color: var(--whiteColor);
  position: relative;
  z-index: 1;
  overflow: hidden;
  padding: 15px 40px 9px 40px;
  font-size: 20px;
  font-weight: 400;
}
.default-btn::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 0;
  z-index: -1;
  background-color: var(--whiteColor);
  transition: var(--transition);
}
.default-btn:hover {
  color: var(--mainColor);
}
.default-btn:hover::before {
  width: 100%;
  height: 100%;
}

.optional-btn {
  text-transform: uppercase;
  border: none;
  transition: var(--transition);
  display: inline-block;
  background-color: transparent;
  color: var(--whiteColor);
  position: relative;
  z-index: 1;
  overflow: hidden;
  padding: 15px 45px 9px 45px;
  font-size: 20px;
  font-weight: 400;
}
.optional-btn::before {
  content: "";
  position: absolute;
  right: 0;
  top: 0;
  width: 0;
  height: 0;
  z-index: -1;
  background-color: var(--whiteColor);
  transition: var(--transition);
}
.optional-btn::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  border: 1px solid var(--whiteColor);
}
.optional-btn:hover {
  color: var(--mainColor);
}
.optional-btn:hover::before {
  width: 100%;
  height: 100%;
}

/*section-title*/
.section-title {
  max-width: 720px;
  text-align: center;
  margin-bottom: 45px;
  margin-left: auto;
  margin-right: auto;
  margin-top: -5px;
}
.section-title .sub-title {
  display: block;
  text-transform: uppercase;
  color: var(--mainColor);
  letter-spacing: 1px;
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 600;
}
.section-title h2 {
  margin-bottom: 0;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 50px;
  font-weight: 600;
}

/*form-control*/
.form-control {
  height: 50px;
  color: var(--whiteColor);
  background-color: #22152c !important;
  display: block;
  width: 100%;
  border-radius: 5px;
  padding: 2px 0 0 15px;
  box-shadow: unset !important;
  border: none;
  transition: var(--transition);
  font-family: var(--fontFamily3);
  font-size: 14px;
  font-weight: 500;
}
.form-control::placeholder {
  color: var(--whiteColor);
  text-transform: uppercase;
  transition: var(--transition);
  letter-spacing: 1px;
}
.form-control:focus::placeholder {
  color: transparent;
}
.form-control::-webkit-search-cancel-button {
  display: none;
}

textarea.form-control {
  height: auto;
  padding-top: 15px;
}

/*keyframes*/
@keyframes rotateme {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes moveleftbounce {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-20px);
  }
  100% {
    transform: translateX(0);
  }
}
