/*Page Title Area CSS*/
.page-title-area {
  text-align: center;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 210px;
  padding-bottom: 80px;
}

.page-title-bg1 {
  background-image: url(assets/img/main-banner-bg1.jpg);
}

.page-title-content img {
  margin-bottom: 35px;
}
.page-title-content .player-image {
  max-width: 120px;
  width: 120px;
}
.page-title-content h1 {
  margin-bottom: 0;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1;
  position: relative;
  display: inline-block;
  animation: glitch 1s linear infinite;
  overflow: hidden;
  font-size: 130px;
  font-weight: 700;
}
.page-title-content h1:before, .page-title-content h1:after {
  content: attr(title);
  position: absolute;
  left: 0;
}
.page-title-content h1:before {
  animation: glitchTop 1s linear infinite;
  clip-path: polygon(0 0, 100% 0, 100% 33%, 0 33%);
}
.page-title-content h1:after {
  animation: glitchBotom 1.5s linear infinite;
  clip-path: polygon(0 67%, 100% 67%, 100% 100%, 0 100%);
}
.page-title-content .sub-title {
  display: block;
  text-transform: uppercase;
  color: var(--mainColor);
  margin-top: -8px;
  font-size: 20px;
  font-weight: 600;
}

@keyframes glitch {
  2%, 64% {
    transform: translate(2px, 0) skew(0deg);
  }
  4%, 60% {
    transform: translate(-2px, 0) skew(0deg);
  }
  62% {
    transform: translate(0, 0) skew(5deg);
  }
}
@keyframes glitchTop {
  2%, 64% {
    transform: translate(2px, -2px);
  }
  4%, 60% {
    transform: translate(-2px, 2px);
  }
  62% {
    transform: translate(13px, -1px) skew(-13deg);
  }
}
@keyframes glitchBotom {
  2%, 64% {
    transform: translate(-2px, 0);
  }
  4%, 60% {
    transform: translate(-2px, 0);
  }
  62% {
    transform: translate(-22px, 5px) skew(21deg);
  }
}
